@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
    font-family: Roboto, "Helvetica Neue", Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji" ;
    line-height: 1.5;
}
.nav--brothertec{
    line-height: 1;
    background-color: #fff;    
    padding-top: 2px;
    padding-bottom: 2px;
}

@media (min-width: 576px){
    .nav--brothertec{
        padding-top: 5px; 
        padding-bottom: 5px; 
    }
}

.navbar-brand {
    padding-top: 0;
    padding-bottom: 0;
}

.nav-imagem {
    width: 205px;
    height: 50px;
}

@media (min-width: 576px){
    .nav-imagem {
        width: 293px;
        height: 72px;
    }
}

.nav--brothertec .nav-link { 
    color: #5F5F5E;
    font-size: 16px;
}

@media (min-width: 576px){
    .nav--brothertec .nav-link { 
        font-size: 20px;
    }
}

.nav--brothertec .nav-link.selected { 
    color: #0063B1;
    font-weight: 500;
}

.nav--brothertec .navbar-toggler {
    color: #0063B1;
    border: 1px solid #D6D6D6;
}

@media(min-width: 992px){
    .nav-item--brothertec{
        padding-left: 48px;
    }
}


.banner {
    background-image: url(/static/media/bg-header.471d0623.png);
    background-repeat: repeat;    
    width: 100%; 
    line-height: 1.1;
    color: #FFFFFF;
    font-weight: 500;
    padding-top: 56px;
    margin-top: 54px;
    
}

@media (min-width: 576px) {
    .banner {
        margin-top: 81px;
    }
}

div .titulo{
    padding-bottom: 42px;
}

.banner h1{
    font-weight: 500;
    font-size: 24px;
    text-align: center;
    
}

@media (min-width: 576px) {
    .banner h1{
        font-size: 40px;
    }
}

.banner h1 span{
    font-size: 35px;
}

@media (min-width: 576px) {
    .banner h1 span{
        font-size: 62px;
    }
}

.icon-container{
    padding-top: 42px;
}

.icon-div{
    flex: 1 1;
}

.icon-title{
    display: block;
    text-align: center;
    padding-bottom: 16px;
    font-size: 20px;
}

@media (min-width: 576px){
    .icon-title{
        font-size: 24px;
    }
}


.icon-image{
    display: block;
    margin: auto;
    width: 64px;
    height: 64px;
    margin-bottom: 72px;
    
}

@media (min-width: 576px){
    .icon-image{
        width: 128px;
        height: 128px;
    }
}

.website{
    width: 80px;
    height: 80px;
    margin-bottom: 32px;
}

@media (min-width: 576px){
    .website{
        width: 128px;
        height: 128px;
    }
}




.integrante {
    color: #303030;
    padding: 10px;
}

.integrante-nome {
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    line-height: 1.3;
    margin: 0;
    padding: 0;
    padding-top: 16px;
}

@media (min-width:576px){
    .integrante-nome {
        font-size: 20px;
    }
}

.integrante-div-imagem{
    text-align: center;
}

.integrante-imagem{
    border-radius: 100%;
    width: 88px;
    height: 88px;
}

.integrante-funcao {
    padding-top: 2px;
    display: block;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.3;
}

@media (min-width:576px){
    .integrante-funcao {
        font-size: 18px;
    }
}

.integrante-descricao {
    padding-top: 16px;
    display: block;
    text-align: center;   
    font-size: 16px;
    line-height: 1.3;
}

@media (min-width:576px){
    .integrante-descricao {
        font-size: 18px;
    }
}
.section-sobre {
    padding: 32px 0 32px 0;
}

@media (min-width: 576px) {
    .section-sobre {
        padding: 64px 0 64px 0;
    }
}

.separador-mobile {
    width: 64px;
    border: 1px solid #C4C4C4;
    background-color: #C4C4C4;
}

@media (min-width: 576px) {
    .separador-mobile {
        display: none;
    }
}

.sobre-titulo {
    font-weight: bold;
    font-size: 24px;
    line-height: 1.5;
    padding-bottom: 24px;
    margin: 0;
}

@media (min-width: 576px) {
    .sobre-titulo {
        font-weight: 500;
        font-size: 40px;
    }
}

.sobre-descricao {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
}

@media (min-width: 576px) {
    .sobre-descricao {
        font-weight: 500;
        font-size: 18px;
    }
}
