.imagem-card{
    border-bottom: 1px solid #C4C4C4;

}

.badge-card{
    width: 60px;
    height: 22px;
    background-color:#818A91;
    border-radius: 4px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
    margin-bottom: 16px;
}

@media (min-width: 576px){
    .badge-card{
        width: 65px;
        margin-top: 4px;
        margin-bottom: 24px;
   

    }
}

.titulo-card{
    font-weight: 500;
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 12px;
    color: #303030;
}

@media (min-width: 576px){
    .titulo-card{
        font-weight: bold;
        font-size: 20px;
        line-height: 1.1;
        margin-bottom: 24px;
    }
}

.texto-card{
    font-size: 16px;
    line-height: 24px;
}

@media (min-width: 576px){
    .texto-card{
        font-size: 18px;
        line-height: 150%;
    }
}

.botao-card{
    background-color: #0063B1;
    border-radius: 4px;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
}
.projetos{
    background-color: #E4F3FF;
    padding: 32px 0;
}

@media (min-width: 576px) {
    .projetos{
        padding: 64px 0;
    }
}
.projeto-titulo{
    font-weight: bold;
    font-size: 24px;
    line-height: 1.5;
    padding-bottom: 24px;
}

@media (min-width: 576px) {
    .projeto-titulo{
        font-weight: 500;
        font-size: 40px;
        padding-bottom: 33px;
    }
}
.footer-brothertec {
    text-align: center;
    padding: 10px 0;
}

.footer-brothertec-text {
    font-size: 14px;
    line-height: 1.5;
    color: #303030;
}
.contato{
    background-image: url(/static/media/bg-header.471d0623.png);
    background-repeat: repeat; 
    padding-top: 32px;
    padding-bottom: 32px; 
}
  
.contato-titulo{
    
    padding-bottom: 25px;
    color: #FFFFFF;
    font-weight: 500;
    font-size: 24px;
    line-height: 1.5;
}

@media (min-width: 576px){
    .contato-titulo{
        font-size: 40px;
    }
}

.form-contato{
margin-bottom: 40px;
}

@media (min-width: 576px){
    .tamanho-contato{
        max-width: 730px;
        margin-left: auto;
        margin-right: auto;

    }
}

.btn-contato{
    color: #373A3C;
    font-size: 16px;
    line-height: 21px;
    background-color: #FFFFFF;
    border: 1px solid #CCCCCC;
    box-sizing: border-box;
    border-radius: 4px;

}

.icone-contato{
    width: 40px;
    height: 52px;
    font-size: 45px;
    line-height: 52px;
    color: #FFFFFF;
   
}

.contato-icone{
    display: inline-block;
    text-align: center;
    width: 100%;
    padding: 0;
}

.icone-meio{
    margin-left: 84px;
    margin-right: 84px;
    
}


